import React, { Component } from "react";
import CourseListItem from "./CourseListItem";
import { data } from "../data/data";

const SearchResults = props => {
  const results = data.map((course, index) => {
      return (
        <CourseListItem 
          key={course.id}
          course={course}
          />
      )
    });

  return (
     <div>
       {results}
     </div> 
    )
}

export default SearchcResults;